import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Home from '../view/Home.vue'

/* eslint-disable*/
export default new Router({
  routes: [
    { path: '/', name: 'Home', component: Home },
    { path: '/MyTarget', name: 'MyTarget', },
    { path: '/SelfAssessment', name:'SelfAssessment', component: resolve => require(['../view/SelfAssessment/SelfAssessment.vue'], resolve),
        children:[
          { path: '/SelfAssessment/AddTarget', name:'AddTarget', component: resolve => require(['../view/SelfAssessment/AddTarget.vue'], resolve) },
          { path: '/SelfAssessment/AboutMe', name: 'AboutMe', component: resolve => require(['../view/SelfAssessment/AboutMe.vue'], resolve),
            children: [
              { path: '/SelfAssessment/AboutMe/MyBackground', component: resolve => require(['../components/AboutMeForm.vue'], resolve) },
              { path: '/SelfAssessment/AboutMe/PE', component: resolve => require(['../components/AboutMeForm02.vue'], resolve) }
            ]
          },
          { path: '/SelfAssessment/result', name:'result', component: resolve => require(['../view/SelfAssessment/result.vue'], resolve) }
        ]

    },
  ]
})



