<template>
  <div class="header">
    <div class="header__logo">
      <div class="header__logo__bottom_1" ></div>
        <img src="../images/component/header/logo.png" alt="logo" class="header__logo__img" >
      <div class="header__logo__bottom_2"></div>
    </div>
    <div class="header__nav">
      <div class="header__navBox " :class="{'header__navBox--BottomLine':!OriclOne}">
        <div class="header__navBox__oricl" >
          <img src="../images/component/header/nav_line.png" alt="" v-show="OriclOne">
        </div>
        <span>首页</span>
      </div>
      <div class="header__navBox " :class="{'header__navBox--BottomLine':!OriclTwo}">
        <div class="header__navBox__oricl">
          <img src="../images/component/header/nav_line.png" alt="" v-show="OriclTwo">
        </div>
        <span style="color:rgba(177, 218, 217, 0.5)">自我评估</span>
      </div>
      <div class="header__navBox" :class="{'header__navBox--BottomLine':!OriclThree}">
        <div class="header__navBox__oricl">
          <img src="../images/component/header/nav_line.png" alt="" v-show="OriclThree">
        </div>
        <span style="color:rgba(177, 218, 217, 0.5)">名校展示</span>
      </div>
    </div>
    <div class="header__right">
      <div class="header__right__login"><a href="#">登入</a><span>&nbsp&nbsp\&nbsp&nbsp</span><a href="#">注册</a></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'header',
  computed: {
    OriclOne () {
      return this.$route.path === '/'
    },
    OriclTwo () {
      return this.$route.path === '/SelfAssessment/AddTarget' || this.$route.path === '/SelfAssessment/AboutMe' || this.$route.path === '/SelfAssessment/AboutMe/MyBackground' || this.$route.path === '/SelfAssessment/AboutMe/PE' || this.$route.path === '/SelfAssessment/result'
    },
    OriclThree () {
      return this.$route.path === '/school'
    }
  }

}
</script>



