<template>
  <div class="circuit">
    <div class="circuit__cent"></div>
    <div class="circuit__right" v-show="type==='right'"></div>
    <div class="circuit__left" v-show="type==='left'"></div>
  </div>
</template>

<script>
export default {
  name: 'circuit',
  props: {
    type: String
  }
}
</script>




