<template>
  <div class="round">
    <div class="round__outside">
      <img src="../images/component/round/round__outside.png" alt="" class="RotateAW" :class="{'paused': paused}">
    </div>
    <div class="round__inside">
      <img src="../images/component/round/round__inside.png" alt="" class="RotateCW" :class="{'paused': paused}">
    </div>
    <div class="round__cent">
      <slot name="round_cent"></slot>
    </div>
    <div class="round__title" @mouseover='open' @mouseout='close'>
      <slot name="title"></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'round',
  data () {
    return {
      paused: false
    }
  },
  methods: {
    open () {
      this.paused = true
      this.$emit('open')
    },
    close () {
      this.paused = false
      this.$emit('close')
    }
  }
}
</script>



